.header {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  height: 10vh;

  color: white;
  transition: 0.5s;
  transition-timing-function: ease-in;
  z-index: 1;
}
.header-bg {
  background-color: rgb(20, 20, 20);
}

.header_container {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_left ul {
  width: 140%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  cursor: pointer;
  
}
.header_right ul {
  width: 180%;
  display: flex;
  justify-content: space-around;
  list-style: none;
  cursor: pointer;

}

@media screen and (max-width: 865px) {
  .header_left ul li:not(:first-child) {
    display: none;
  }
}
